<template>
  <v-container>

    <v-row
      justify="center"
    >
      <v-col
        cols="12"
        lg="4"
        md="6"
        sm="8"
      >
        <v-card>
          
          <v-card-title>Sign In</v-card-title>
          
          <v-card-text>

            <v-text-field
              label="Email"
              type="email"

              v-model="email"
            ></v-text-field>

            <v-text-field
              label="Password"
              type="password"

              v-model="password"
            ></v-text-field>

          </v-card-text>

          <v-card-actions>
            <v-btn
              text

              @click="onSignIn"
            >
              Sign In
            </v-btn>
          </v-card-actions>

        </v-card>
      </v-col>
    </v-row>
  </v-container>  
</template>

<script>
import * as firebase from 'firebase'

export default {
  name: 'Signup',
  data: () => ({

    email: null,
    password: null,

  }),

  methods: {
    onSignIn () {
      firebase.auth().signInWithEmailAndPassword(this.email, this.password)
        .catch(error => console.log(error))
        .then(
          user => {
            console.log(user.user, user.additionalUserInfo, user.credential, user.operationType)
          }
        )
    }
  },
}
</script>

